.highlight-btn {
  align-items: center;
  appearance: none;
  background-image: radial-gradient(
    100% 100% at 100% 0,
    #5adaff 0,
    #5468ff 100%
  );
  border: 0;
  border-radius: 1rem;
  box-shadow: rgba(45, 35, 66, 0.4) 0 0.2rem 0.4rem,
    rgba(45, 35, 66, 0.3) 0 0.7rem 1.3rem -0.3rem,
    rgba(58, 65, 111, 0.5) 0 -0.3rem 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-flex;

  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  padding-left: 1.6rem;
  padding-right: 1.6rem;

  text-align: left;
  text-decoration: none;
  transition: box-shadow 0.15s, transform 0.15s;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow, transform;
}

.highlight-btn:focus {
  box-shadow: #3c4fe0 0 0 0 0.15rem inset, rgba(45, 35, 66, 0.4) 0 0.2rem 0.4rem,
    rgba(45, 35, 66, 0.3) 0 0.7rem 1.3rem -0.3rem, #3c4fe0 0 -0.3rem 0 inset;
}

.highlight-btn:hover {
  box-shadow: rgba(45, 35, 66, 0.4) 0 0.4rem 0.8rem,
    rgba(45, 35, 66, 0.3) 0 0.7rem 1.3rem -0.3rem, #3c4fe0 0 -0.3rem 0 inset;
  transform: translateY(-0.2rem);
}

.highlight-btn:active {
  box-shadow: #3c4fe0 0 0.3rem 0.7rem inset;
  transform: translateY(0.2rem);
}

.light-btn {
  align-items: center;
  appearance: none;
  background-color: #f8f9fa;
  border-radius: 0.4rem;
  border-width: 0;
  box-shadow: rgba(45, 35, 66, 0.4) 0 0.2rem 0.4rem,
    rgba(45, 35, 66, 0.3) 0 0.7rem 1.3rem -0.3rem, #d6d6e7 0 -0.3rem 0 inset;
  box-sizing: border-box;
  color: #444;
  cursor: pointer;
  display: inline-flex;

  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  padding-left: 1.6rem;
  padding-right: 1.6rem;
  position: relative;
  text-align: left;
  text-decoration: none;
  transition: box-shadow 0.15s, transform 0.15s;

  touch-action: manipulation;

  will-change: box-shadow, transform;
}

.light-btn:focus,
.light-btn:focus {
  box-shadow: #d6d6e7 0 0 0 0.15rem inset, rgba(45, 35, 66, 0.4) 0 0.2rem 0.4rem,
    rgba(45, 35, 66, 0.3) 0 0.7rem 1.3rem -0.3rem, #d6d6e7 0 -0.3rem 0 inset;
}

.light-btn:hover,
.light-btn:hover {
  box-shadow: rgba(45, 35, 66, 0.4) 0 0.4rem 0.8rem,
    rgba(45, 35, 66, 0.3) 0 0.7rem 1.3rem -0.3rem, #d6d6e7 0 -0.3rem 0 inset;
  transform: translateY(-0.2rem);
}

.light-btn:active,
.light-btn:active {
  box-shadow: #d6d6e7 0 0.3rem 0.7rem inset;
  transform: translateY(0.2rem);
}

.tint-blue {
  color: #228be6;
}

.tint-red {
  color: red;
}

.tint-green {
  stroke: #51cf66;
  color: #51cf66;
}

.tint-yellow {
  color: #f08c00;
}

.tint-black {
  color: #333;
}

.tint-purple {
  color: #5468ff;
}

.hide {
  opacity: 0;
  visibility: hidden;
  display: none;
}
